// src/app/login/page.js
"use client";

import React, { useState, useContext } from "react";
import { useRouter } from "next/navigation";
import { auth } from "@/firebase/firebase";
import { signInWithEmailAndPassword, createUserWithEmailAndPassword } from "firebase/auth";
import { AuthContext } from "@/context/AuthContext";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// Initialize toast notifications (you can also place this in a global component)
if (typeof window !== "undefined") {
  toast.configure();
}

export default function LoginPage() {
  const router = useRouter();
  const { user } = useContext(AuthContext);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  // If already logged in, redirect to dashboard
  if (user) {
    router.replace("/dashboard");
    return null;
  }

  const handleLogin = async () => {
    setError("");
    try {
      await signInWithEmailAndPassword(auth, email, password);
      toast.success("Logged in successfully!");
      router.replace("/dashboard");
    } catch (err) {
      setError(err.message);
      toast.error("Login failed: " + err.message);
    }
  };

  const handleRegister = async () => {
    setError("");
    try {
      await createUserWithEmailAndPassword(auth, email, password);
      toast.success("Registered successfully!");
      router.replace("/dashboard");
    } catch (err) {
      setError(err.message);
      toast.error("Registration failed: " + err.message);
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100 p-4">
      <h1 className="text-4xl font-bold mb-6">Login / Register</h1>
      {error && <p className="mb-4 text-red-500">{error}</p>}
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        className="mb-4 p-2 border rounded w-80"
      />
      <input
        type="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        className="mb-4 p-2 border rounded w-80"
      />
      <div className="flex space-x-4">
        <button onClick={handleLogin} className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transform hover:scale-105 transition">
          Login
        </button>
        <button onClick={handleRegister} className="px-4 py-2 bg-green-500 text-white rounded hover:bg-green-600 transform hover:scale-105 transition">
          Register
        </button>
      </div>
    </div>
  );
}
